.operators {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding: 3em 2em;
}

.operator-item {
  margin-top: 5em;
  display: block;
  height: 150px;
  width: 90%;
  color: #353535;
  text-decoration: none;
  text-align: center;
  transition: .15s ease;

  &:hover {
    color: #3d8ae5;

    .logo {
      border-color: #69a4ea;
      box-shadow: 0 0 0.7em 0 #3d8ae5;
    }
  }

  &:first-child {
    margin-top: 0;
  }

  .logo {
    padding: 1.5em;
    height: 100%;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 1.6em;
    box-shadow: 0 0 0.3em 0 rgba(50,50,50,.5);
    background: linear-gradient(to bottom, #fff, #f5f5f5);
    transition: .15s ease;

    .logo-image {
      height: 100%;
      width: 100%;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  span {
    padding-top: .5em;
    display: block;
    text-align: center;
    font-size: 1.4em;
  }
}

@media screen and (min-width: 480px) {
  .operators {
    flex-flow: row;
    flex-wrap: wrap;
  }

  .operator-item {
    margin-top: 0;
    margin-right: 19.9999px;
    height: 160px;
    width: calc((100% - 40px) / 3);

    .logo {
      padding: 2em;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

@media screen and (min-width: 900px) {
  .operator-item {
    height: 180px;
    width: calc((100% - 60px) / 4);
  }
}

@media screen and (min-width: 1200px) {
  .operator-item {
    height: 200px;
    width: calc((100% - 60px) / 4);
  }
}
