header {
  display: flex;
  justify-content: space-between;
  padding: 10px 5px;
  height: 78px;
  box-shadow: 0 0 1em 0 #363636;
  color: #fff;
  background-color: #363636;

  a {
    display: block;
    width: 120px;
  }

  ul {
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    li {
      display: flex;
      text-align: center;

      a {
        position: relative;
        color: #e9e9e9;
        text-decoration: none;
        text-transform: uppercase;

        &::before {
          content: '';
          display: block;
          left: 0;
          bottom: -29px;
          width: 100%;
          height: 2px;
          position: absolute;
          background-color: #e9e9e9;
          transform: scaleX(0);
        }

        &.active::before {
          transform: scaleX(1);
          transition: .3s ease-in-out;
        }
      }
    }
  }
}
